body {
  margin: 0;
  padding: 0;
  background-color: #f5f5f5;
  color: #000;
  font-size: 16px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}
html {
  box-sizing: border-box;
}
* {
  box-sizing: inherit;
}
*:focus {
  outline: none;
}
a {
  color: #000;
  text-decoration: none;
}
ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
button {
  border: none;
  font-family: 'Open Sans', sans-serif;
  background: none;
}
img {
  max-width: 100%;
  margin: 20px 0;
}
input {
  border: none;
  font-family: 'Open Sans', sans-serif;
  background: none;
}
fieldset {
  margin: 0;
  padding: 0;
  border: none;
}
.visually-hidden {
  position: absolute;
  height: 1px;
  width: 1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
}
.main {
  position: relative;
  width: 1450px;
  display: flex;
  justify-content: space-between;
  margin: 50px auto 0;
}
.disable {
  display: none;
}
.bold {
  font-weight: 600;
}
.notice {
  margin-bottom: 35px;
  margin-top: 35px;
  padding: 18px 24px;
  text-indent: 32px;
  font-size: 16px;
  line-height: 22px;
  color: #000;
  font-weight: 400;
  border-radius: 15px;
  background-color: #EFF0F2;
  background-image: url('../img/light.png');
  background-size: 25px;
  background-repeat: no-repeat;
  background-position: 20px 13px;
}
.book {
  background-image: url('../img/book.png');
}
.code {
  padding: 0 5px;
  background-color: #e0e0e0;
  border-radius: 5px;
}
.right {
  border-color: #008000 !important;
  background-color: #D6EDD7;
}
.wrong {
  border-color: #A31515 !important;
  background-color: #F8CBCB;
}
.square {
  display: inline-block;
  padding: 1px 7px;
  font-weight: 800;
  border: 1px solid #909090;
  border-radius: 5px;
}
.finded {
  animation: finded;
  animation-duration: 3s;
  animation-iteration-count: 3;
}
@font-face {
  font-family: 'Open Sans';
  src: url(../fonts/OpenSans-Regular.ttf) format('ttf'), url('../fonts/opensans-regular-webfont.woff2') format('woff2'), url('../fonts/opensans-regular-webfont.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Open Sans';
  src: url(../fonts/OpenSans-SemiBold.ttf) format('ttf'), url('../fonts/opensans-semibold-webfont.woff2') format('woff2'), url('../fonts/opensans-semibold-webfont.woff') format('woff');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Open Sans';
  src: url(../fonts/OpenSans-Bold.ttf) format('ttf'), url('../fonts/opensans-bold-webfont.woff2') format('woff2'), url('../fonts/opensans-bold-webfont.woff') format('woff');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Montserrat';
  src: url(../fonts/montserrat-regular.ttf) format('ttf'), url('../fonts/montserrat-regular.woff2') format('woff2'), url('../fonts/montserrat-regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Montserrat';
  src: url(../fonts/montserrat-medium.ttf) format('ttf'), url('../fonts/montserrat-medium.woff2') format('woff2'), url('../fonts/montserrat-medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Montserrat';
  src: url(../fonts/montserrat-semibold.ttf) format('ttf'), url('../fonts/montserrat-semibold.woff2') format('woff2'), url('../fonts/montserrat-semibold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes finded {
  0% {
    background-color: #fff;
  }
  50% {
    background-color: #BED6E0;
  }
  100% {
    background-color: #fff;
  }
}
.course {
  width: 1450px;
  padding: 60px 10px 0px 525px;
}
.course__search {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 52px;
  margin-bottom: 50px;
}
.course__search-input {
  width: 100%;
  height: 100%;
  padding: 16px 110px 16px 67px;
  background-color: #EFF0F2;
  border-radius: 15px;
  background-image: url('../img/search.png');
  background-size: 25px 25px;
  background-repeat: no-repeat;
  background-position: 20px 13px;
}
.course__search-btn {
  position: absolute;
  top: 0;
  right: 0;
  width: 110px;
  height: 100%;
  border: 1px solid #909090;
  border-radius: 15px;
  font-size: 16px;
  line-height: 16px;
  color: #000;
  font-weight: 400;
  font-weight: 500;
  cursor: pointer;
}
.course__theme {
  margin-bottom: 30px;
  font-size: 16px;
  line-height: 22px;
  color: #000;
  font-weight: 400;
}
.course__title {
  margin: 0;
  padding: 0;
  margin-bottom: 8px;
  font-size: 24px;
  line-height: 24px;
  color: #000;
  font-weight: 400;
}
.course__progress {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.course__title-h3 {
  font-size: 20px;
  line-height: 24px;
  color: #000;
  font-weight: 400;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
}
.course__btn {
  width: 250px;
  height: 52px;
  font-size: 16px;
  line-height: 22px;
  color: #000;
  font-weight: 400;
  font-weight: 500;
  color: #fff;
  background-color: #61A5C2;
  border-radius: 20px;
  cursor: pointer;
  transition: 0.2s;
}
.course__btn:hover {
  background-color: #BED6E0;
}
.course__item {
  position: relative;
  padding-left: 23px;
}
.course__item:before {
  content: '';
  position: absolute;
  top: 10px;
  left: 10px;
  display: block;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #000;
}
.course__title-h4 {
  margin: 0;
  font-size: 16px;
  line-height: 22px;
  color: #000;
  font-weight: 400;
}
.course__list {
  margin-bottom: 20px;
}
.course__list-0 {
  margin-bottom: 0;
}
.course__table {
  margin-bottom: 35px;
  border-color: #909090;
}
.course__table caption {
  margin-bottom: 5px;
  text-align: left;
}
.course__table th {
  text-align: left;
}
.course__table th,
.course__table td {
  padding: 9px 11px;
}
.course__code {
  position: relative;
  margin-bottom: 35px;
}
.course__code-result {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
.course__code-result input {
  width: 100%;
  height: 52px;
  background-color: #fff;
  border: 1px solid #909090;
  border-radius: 15px;
}
.course__code-result button {
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 15px;
}
.course__example {
  margin-bottom: 20px;
  padding: 20px;
  border: 1px solid #909090;
  border-radius: 15px;
}
.course__example-1 {
  margin-bottom: 100px;
  padding: 20px;
  border: 1px solid #909090;
  border-radius: 15px;
}
.course__example-options {
  display: flex;
  margin-bottom: 20px;
}
.course__example-option {
  margin-right: 10px;
  padding: 11px;
  border: 1px solid #909090;
  border-radius: 15px;
  cursor: pointer;
}
.course__example-bot {
  display: flex;
  margin-bottom: 20px;
  padding-top: 20px;
  padding: 20px 3px;
  border-top: 3px solid #D9D9D9;
}
.course__example-bot.wrong {
  border-radius: 5px;
}
.course__example-bot.right {
  border-radius: 5px;
}
.course__example-answers {
  display: flex;
  align-items: top;
}
.course__example-voids {
  display: flex;
}
.course__example-empty {
  width: 127px;
  height: 46px;
  margin-right: 10px;
  border: 1px solid #909090;
  border-radius: 15px;
  background-color: #EFF0F2;
}
.course__example-result {
  width: 176px;
  height: 52px;
  padding: 11px;
}
.course__example-2 {
  padding: 20px;
  border: 1px solid #909090;
  border-radius: 15px;
}
.course__example-area {
  width: 100%;
  height: 140px;
  margin-bottom: 20px;
  padding: 20px;
  border: 2px solid #909090;
  border-radius: 15px;
  resize: none;
}
.course__example-btns {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.course__btn-gray {
  color: #919395;
  background-color: #eef0f2;
}
.course__labels {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}
.course__label {
  margin-bottom: 20px;
  cursor: pointer;
}
.course__img-wrapper {
  display: flex;
  justify-content: center;
}
.course__table--2 {
  width: 100%;
}
.course__table--2 th {
  background-color: #f0f0f0;
}
.course__table--2 td {
  width: 50%;
}
.course__img-12 {
  background-color: green;
}
.course__input-run {
  padding: 10px;
}
input[type="checkbox"],
input[type="radio"] {
  display: none;
}
input[type="checkbox"]:checked + span:after,
input[type="radio"]:checked + span:after {
  content: '';
  position: absolute;
  top: 8px;
  left: 9px;
  width: 7px;
  height: 7px;
  background-color: #909090;
  border-radius: 50%;
}
.course__labels-checkbox label {
  position: relative;
  padding-left: 31px;
}
.course__labels-checkbox span::before {
  content: '';
  position: absolute;
  top: -1px;
  left: 0;
  width: 23px;
  height: 23px;
  border: 1px solid #909090;
  border-radius: 5px;
}
.course__labels-checkbox input.right + span:before {
  background-color: #D6EDD7;
  border-color: #008000;
}
.course__labels-checkbox input.wrong + span:before {
  border-color: #A31515;
  background-color: #F8CBCB;
}
.course__labels-radio label {
  position: relative;
  padding-left: 31px;
}
.course__labels-radio span::before {
  content: '';
  position: absolute;
  top: -1px;
  left: 0;
  width: 23px;
  height: 23px;
  border: 1px solid #909090;
  border-radius: 20px;
}
.course__labels-radio input.right + span:before {
  background-color: #D6EDD7;
  border-color: #008000;
}
.course__labels-radio input.wrong + span:before {
  border-color: #A31515 !important;
  background-color: #F8CBCB;
}
.bar {
  position: fixed;
  top: 50%;
  left: 50%;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  width: 400px;
  height: 850px;
  padding: 55px 0px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  border-radius: 20px;
  background-color: #EFF0F2;
  transform: translate(-175%, -50%);
}
.bar__lists {
  padding-bottom: 100px;
  padding: 0 45px;
  overflow: hidden;
  overflow-y: scroll;
}
.bar__title {
  margin: 0;
  padding: 0;
  margin-bottom: 28px;
  padding: 0 45px;
  font-size: 24px;
  line-height: 24px;
  color: #000;
  font-weight: 400;
  font-weight: 700;
}
.bar__name {
  margin: 0;
  padding: 0;
  margin-bottom: 28px;
  padding: 0 45px;
  font-size: 20px;
  line-height: 20px;
  color: #000;
  font-weight: 400;
  font-weight: 500;
}
.bar__list-theme {
  font-size: 16px;
  line-height: 20px;
  color: #000;
  font-weight: 400;
}
.bar__item-lesson {
  position: relative;
  margin-bottom: 11px;
  padding: 6px 15px;
  border-radius: 15px;
  transition: 0.2s;
}
.bar__item-lesson:first-child {
  font-weight: 600;
}
.bar__item-lesson:not(:first-child) {
  padding-left: 40px;
  padding-right: 50px;
  cursor: pointer;
}
.bar__item-lesson:not(:first-child)::before {
  left: 15px;
}
.bar__item-lesson:not(:first-child):hover {
  background-color: #BED6E0;
}
.bar__item-lesson.active {
  color: #fff;
  border-radius: 15px;
  background-color: #61A5C2;
}
.bar__item-lesson::before {
  content: '1';
  position: absolute;
  top: 6px;
  left: -5px;
  display: block;
}
.bar__item-lesson:nth-child(2)::before {
  content: '1.1';
}
.bar__item-lesson:nth-child(3)::before {
  content: '1.2';
}
.bar__item-lesson:nth-child(4)::before {
  content: '1.3';
}
.bar__list-lesson-2 li:nth-child(1)::before {
  content: '2';
}
.bar__list-lesson-2 li:nth-child(2)::before {
  content: '2.1';
}
.bar__list-lesson-2 li:nth-child(3)::before {
  content: '2.2';
}
.bar__list-lesson-2 li:nth-child(4)::before {
  content: '2.3';
}
.bar__list-lesson-3 li:nth-child(1)::before {
  content: '3';
}
.bar__list-lesson-3 li:nth-child(2)::before {
  content: '3.1';
}
.bar__list-lesson-3 li:nth-child(3)::before {
  content: '3.2';
}
.bar__list-lesson-3 li:nth-child(4)::before {
  content: '3.3';
}
.bar__list-lesson-4 li:nth-child(1)::before {
  content: '4';
}
.bar__list-lesson-4 li:nth-child(2)::before {
  content: '4.1';
}
.bar__list-lesson-4 li:nth-child(3)::before {
  content: '4.2';
}
.bar__list-lesson-4 li:nth-child(4)::before {
  content: '4.3';
}
.bar__btn {
  align-self: self-start;
  margin-bottom: 20px;
  margin-left: 45px;
  padding: 16px 14px;
  border-radius: 20px;
  font-size: 16px;
  line-height: 16px;
  color: #000;
  font-weight: 400;
  cursor: pointer;
  transition: 0.2s;
}
.bar__btn.active {
  color: #fff;
  border: none;
  background-color: #61A5C2;
}
.bar__btn:hover {
  background-color: #BED6E0;
}
